import React from 'react';
import { Paper, Typography } from '@material-ui/core';

export const VideoDetail = ({mainVideo}) => {
  if(!mainVideo) return(<div>Loading...</div>);
  const videoSrc = 'https://www.youtube.com/embed/'+mainVideo.id.videoId
  return (
    <>
      <Paper elevation={6} style={{height:'70%'}}>
        <iframe frameBorder='0' height='100%' width='100%' title='Video Player' src={videoSrc}></iframe>
      </Paper>
      <Paper elevation={6} style={{padding:'15px'}}>
        <Typography variant='h5'>{mainVideo.snippet.title} - {mainVideo.snippet.channelTitle}</Typography>
        <Typography variant='subtitle1'>{mainVideo.snippet.channelTitle}</Typography>
        <Typography variant='subtitle2'>{mainVideo.snippet.description}</Typography>
      </Paper>
    </>
  )
}
